










import { Component, Vue } from 'vue-property-decorator';
const ReportTimer = () => import('@/application/Report/View/ReportTimer.vue').then(m => m.default);
const ReportTabs = () => import('@/application/Report/View/ReportTabs.vue').then(m => m.default);
import { TimeOptions } from '@/globalInterface';
import LotteryCard from '@/application/Lottery/View/LotteryCard.vue';

@Component({
  components: {
    ReportTimer,
    ReportTabs,
    LotteryCard,
  }
})
export default class Report extends Vue {
  private gameType?: string = 'pk10';
  private time?: TimeOptions = {
    current: 180,
    max: 200,
  };
  private apiCall() {
    // NOTE api simulation
    setTimeout(() => {
      const tm = (Math.random() * 10) + 10;
      this.time = {
        current: tm,
        max: tm - 2,
      }
    }, 3000)
  }
}
